body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #fff;
  overflow-x: hidden;
  position: relative;
}

p {
  color: #242423;
  font-size: 2rem;
  text-align: justify;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.ml-40 {
  margin-left: 4rem;
}

.mensaje_form {
  position: fixed;
  z-index: 0;
  bottom: 2%;
  left: 2%;
  right: 2%;
  max-width: 40rem;
  padding: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  background-color: #242423;
  box-shadow: 3px 4px 15px rgba(34, 34, 34, 0.3);
  -webkit-transition: 0.3s ease-in;
  transition: 0.3s ease-in;
  transform: translateY(0%);
  font-weight: 700;
  font-size: 2.3rem;
  color: #f8f8f8;
  margin: 10px 0 10px 0;
}

.none {
  display: none;
  transform: translateX(-30px);
}

.loader {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-color: #242423;
  position: absolute;
}

.loader > div {
  height: 100px;
  width: 100px;
  border: 10px solid #f8f8f8;
  border-top-color: #F5CB5C;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 50%;
  animation: spin 1.5s infinite linear;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
.content-full {
  display: none;
}

