/** UI **/
.video {
  width: 100%;
  min-height: 100vh;
  position: relative;
  height: 100%;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: -moz-linear-gradient(45deg, rgba(51, 53, 51, 0.62) 0%, rgba(36, 36, 35, 0.56) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, rgba(51, 53, 51, 0.62) 0%, rgba(36, 36, 35, 0.56) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, rgba(51, 53, 51, 0.62) 0%, rgba(36, 36, 35, 0.56) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#9e333533", endColorstr="#8f242423",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  width: 100%;
  object-fit: cover;
  height: 100%;
  position: absolute;
  z-index: -2;
}

.contenido-video {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  position: relative;
  z-index: 0;
}
.contenido-video h2 {
  font-weight: 700;
  color: #f8f8f8;
  font-size: 4rem;
  margin-top: -2rem;
}
.contenido-video h2 span {
  color: #F5CB5C;
}

.navbar {
  background-color: #242423;
  box-shadow: 0 0.3rem 0.4rem #F5CB5C;
  padding-top: 0.4rem;
  min-height: 8vh;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar .burger div {
  width: 2.5rem;
  height: 0.3rem;
  margin-top: 0.5rem;
  background-color: #f8f8f8;
  margin-bottom: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
}
.navbar .nav {
  position: absolute;
  right: 0px;
  height: 50vh;
  top: 6vh;
  background: #242423;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  transform: translateX(100%);
  transition: transform 0.5s ease-in;
}
.navbar .burger {
  display: block;
}
.navbar.fijo {
  bottom: inherit;
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
}
.navbar .nav-active {
  opacity: 1;
  transform: translateX(0%);
}
.navbar .toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}
.navbar .toggle .line2 {
  opacity: 0;
}
.navbar .toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}
@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}
.navbar a {
  color: #f8f8f8;
  font-weight: 300;
  font-size: 1.4rem;
  text-transform: uppercase;
  opacity: 0;
}
.navbar a:hover {
  text-decoration: line-through #F5CB5C;
}

@media (min-width: 768px) {
  .navbar {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    position: relative;
  }
  .navbar .nav {
    display: block;
    position: relative;
    right: 0px;
    height: 4vh;
    top: 0;
    display: flex;
    flex-direction: row;
    transform: translateX(0%);
  }
  .navbar a {
    opacity: 1;
  }
  .navbar .burger {
    display: none;
  }
}
.aboutMe {
  background-color: #242423;
  height: auto;
}
.aboutMe .download-resume {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 3rem;
}
.aboutMe .about-title {
  margin-top: 0;
  background-color: #F5CB5C;
  font-weight: 700;
  margin-bottom: 5rem;
  text-transform: uppercase;
  font-size: 2.7rem;
}
@media (min-width: 1024px) {
  .aboutMe .about-content {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(3, 1fr);
  }
}
.aboutMe .about-content {
  padding-right: 2rem;
}
.aboutMe .about-content .list-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.aboutMe .about-content .text {
  background-color: #f8f8f8;
  padding: 3rem;
  margin-bottom: 3rem;
}
.aboutMe .about-content .text h3 {
  font-weight: 700;
}
.aboutMe .about-content .text h4 {
  color: #F5CB5C;
  margin-bottom: 0;
}
.aboutMe .about-content .iconos {
  display: flex;
  height: 150px;
  width: 150px;
  justify-content: space-evenly;
  align-items: center;
  background-color: #F5CB5C;
  border-radius: 50%;
  margin-bottom: -6rem;
  z-index: 0;
}
.aboutMe .about-content .iconos i {
  font-size: 2rem;
  color: #242423;
}

@media (min-width: 768px) {
  .skills {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.formulario {
  height: 90vh;
  background-image: url(../img/contact-hero.jpg);
  margin-bottom: 0;
  background-position: center left;
  position: relative;
}
.formulario .formulario-content {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .formulario .formulario-content {
    flex-direction: row;
  }
}
.formulario .formulario-content .contact-title {
  background-color: #F5CB5C;
  font-size: 3rem;
  text-transform: uppercase;
  color: #242423;
  padding: 0 10rem;
  font-weight: 700;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario .formulario-content .contact-title {
    width: auto;
  }
}
.formulario .formulario-content form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.formulario .formulario-content form input:not([type=submit]), .formulario .formulario-content form textarea {
  background-color: #333533;
  border: none;
  color: #f8f8f8;
  outline: none;
  margin-bottom: 1rem;
  padding: 1rem;
  width: 90%;
}
@media (min-width: 768px) {
  .formulario .formulario-content form input:not([type=submit]), .formulario .formulario-content form textarea {
    width: 50%;
  }
}
.formulario .formulario-content form textarea {
  height: 15rem;
}
.formulario .formulario-content form label {
  color: #f8f8f8;
  flex-grow: 1;
}
.formulario .formulario-content form input[type=submit] {
  width: 30%;
  text-align: center;
}

.projects {
  background-color: #F5CB5C;
  padding-bottom: 3rem;
